<script>
  import { Navigate } from 'svelte-router-spa'
  import { fly } from 'svelte/transition'
  import axios from 'axios'
  import Icon from 'svelte-awesome'
  import { refresh } from 'svelte-awesome/icons'

  let step = 1
  let emailMessage
  let passwordMessage
  let registrationErrorMessage
  let registrationSuccessMessage
  let registrationLoading = false

  // organize the registration form state in an object
  let registrationForm = {
    email: null,
    schoolName: null,
    password: null,
    passwordConfirm: null,
  }

  const checkEmail = () => {
    const regEx = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
    if (!registrationForm.email.match(regEx)) {
      return { valid: false, message: 'Email address is invalid' }
    }
    return {
      valid: true,
    }
  }

  const checkPassword = () => {
    const regEx = /^(?=.*\d)(?=.*[a-z])(?=.*[A-Z])(?=.*[^a-zA-Z0-9])(?!.*\s).{8,15}$/
    if (!registrationForm.password.match(regEx)) {
      return {
        valid: false,
        message: 'Password must be 8 characters in length and contain a combination of upper case letters, lower case letters and numbers or special characters',
      }
    }
    if (registrationForm.password !== registrationForm.passwordConfirm) {
      return {
        valid: false,
        message: 'Passwords do not match',
      }
    }
    return {
      valid: true,
    }
  }

  const completeStep1 = () => {
    emailMessage = null
    const { valid, message } = checkEmail()
    if (!valid) {
      emailMessage = message
      return
    }
    step = 2
  }

  const register = async () => {
    passwordMessage = null
    const { valid, message } = checkPassword()

    if (!valid) {
      passwordMessage = message
      return
    }

    try {
      registrationLoading = true
      await axios.post(`${process.env.API_ROOT}/api/users`, registrationForm)
      registrationLoading = false

      registrationSuccessMessage = 'Obrigado por se registrar, redirecionando para login ...'
      await new Promise(f => setTimeout(f, 1800)) // 1.8 second timeout

      // redirect the user
      window.history.replaceState(null, null, '/admin/manage-menus')
      window.location.reload()
    } catch (e) {
      registrationLoading = false
      registrationErrorMessage = e.response.data.message
    }
  }
</script>

<div class="notification">
  <div class="columns">
    <div class="column">
      <div class="content">
        <h1>Publique facilmente seus menus escolares</h1>
        <p>Facilitamos a publicação do menu semanal na web e o fornecemos aos alunos e pais em um formato compatível com dispositivos móveis.</p>
        <p>Basta definir os detalhes e clicar em publicar! Registre-se e comece hoje.</p>
        <!-- <img  src="images/example.png" style="border: 1px solid lightgrey; border-radius: 2px; max-height: 250px;" alt="img"/> -->
      </div>
    </div>
    <div class="column">
      <div class="content">

        <h1>Registrar agora</h1>
        <p>É gratuito e fácil de começar.</p>
        {#if step === 1}
          <div class="field">
            <label class="label">Endereço de e-mail</label>
            <div class="control">
              <input bind:value="{registrationForm.email}" type="text" placeholder="Insira o seu endereço de email" class="input" />
            </div>
            <!-- ADD THE IF BLOCK HERE -->
            {#if emailMessage}
              <p class="help is-danger">{emailMessage}</p>
            {/if}
          </div>
          <div class="field">
            <label class="label">Nome da escola</label>
            <div class="control">
              <input bind:value="{registrationForm.schoolName}" type="text" placeholder="Digite o nome da sua escola" class="input" />
            </div>
          </div>

          <div class="field is-grouped">
            <div class="control">
              <button on:click="{() => completeStep1()}" class="button is-link is-outlined">Seguinte</button>
            </div>
          </div>
        {:else if step === 2}
          <div id="passwordBlock" in:fly="{{ x: 400, duration: 1000 }}">
            <div class="field">
              <label class="label">Senha</label>
              <div class="control">
                <input bind:value="{registrationForm.password}" type="password" placeholder="Coloque sua senha" class="input" />
              </div>
            </div>
            <div class="field">
              <label class="label">Confirme sua senha</label>
              <div class="control">
                <input bind:value="{registrationForm.passwordConfirm}" type="password" placeholder="Confirme sua senha" class="input" />
              </div>
              {#if passwordMessage}
                <p class="help is-danger">{passwordMessage}</p>
              {/if}
            </div>
            {#if registrationLoading}
              <Icon spin data="{refresh}" scale="3" />
            {/if}
            {#if registrationErrorMessage}
              <div class="notification is-warning">{registrationErrorMessage}</div>
            {/if}
            {#if registrationSuccessMessage}
              <div class="notification is-info">{registrationSuccessMessage}</div>
            {/if}
            <div class="field is-grouped">
              <div class="control">
                <button on:click="{register}" class="button is-link is-outlined">Enviar</button>
              </div>
            </div>
          </div>
        {/if}
      </div>
    </div>
  </div>

  <!-- <div class="columns">
    <div class="column">
      <div class="content">
        <h3>Simple Online Editing</h3>
        <p>Just set the details and hit publish! Register and get started today.</p>
      </div>
    </div>
    <div class="column">
      <center>
        <img src="images/example.png" style="border: 1px solid;" />
      </center>
    </div>
  </div> -->

</div>

<div class="container">
  <div class="content" style="line-height: 30px;">
    <center>
      <span>Já registrado?</span>
      <Navigate.default to="/admin/manage-menus">
        <button class="button is-small is-text">Conecte-se</button>
      </Navigate.default>
    </center>
  </div>
</div>
